import React, { useState, useEffect, useMemo } from 'react';
import { Link } from 'react-router-dom';
import SkeletonCard from '../components/SkeletonCard';

// --- Helper Functions ---
const getPriceValue = (priceString) => {
    if (!priceString) return 0;
    const price = parseFloat(String(priceString).replace(',', '.').replace(' zł', ''));
    return isNaN(price) ? 0 : price;
};

const getLowestPrice = (product) => {
    if (!product || !product.heightOptions || product.heightOptions.length === 0) return 0;
    const prices = product.heightOptions.map(opt => getPriceValue(opt.promoPrice || opt.price));
    return Math.min(...prices);
};

const getCheapestOption = (product) => {
    if (!product || !product.heightOptions || product.heightOptions.length === 0) return null;

    return product.heightOptions.reduce((cheapest, current) => {
        const cheapestPrice = getPriceValue(cheapest.promoPrice || cheapest.price);
        const currentPrice = getPriceValue(current.promoPrice || current.price);
        return currentPrice < cheapestPrice ? current : cheapest;
    });
};

const hasPromotion = (product) => {
    if (!product || !product.heightOptions) return false;
    return product.heightOptions.some(opt => opt.promoPrice);
};

const getUniqueValues = (products, key) => {
    const allValues = products.flatMap(p => p[key] || []);
    return ['all', ...Array.from(new Set(allValues))];
};

const colorMap = {
    "Biały": "#FFFFFF",
    "Zielony": "#28a745",
    "Żółty": "#ffc107",
    "Czarny": "#000000",
    "Szary": "#6c757d",
    "Jasny brąz": "#D2B48C",
    "Niebieski": "#007bff",
    "Czerwony": "#dc3545",
    "Jasny Brąz": "#D2B48C",
};

// --- Component ---
function ProductsPage() {
    // --- State ---
    const [products, setProducts] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [err, setErr] = useState(null);
    const [searchTerm, setSearchTerm] = useState('');
    const [filters, setFilters] = useState({ category: 'all', color: 'all', promotion: false, isNew: false });
    const [sortOrder, setSortOrder] = useState('default');
    const [priceRange, setPriceRange] = useState([0, 100]);
    const [minMaxPrice, setMinMaxPrice] = useState([0, 100]);
    const [modalProduct, setModalProduct] = useState(null);

    // --- helper to resolve asset paths (works with relative paths in JSON and absolute URLs)
    const resolveAsset = (path) => {
        if (!path) return '';
        if (path.startsWith('http') || path.startsWith('data:')) return path;
        const prefix = process.env.PUBLIC_URL || '';
        return prefix + (path.startsWith('/') ? path : '/' + path);
    };

    // --- Data Loading and Initial Setup ---
    const loadProducts = async () => {
        setIsLoading(true);
        setErr(null);
        try {
            const url = `${process.env.PUBLIC_URL || ''}/products.json`;
            const res = await fetch(url);
            if (!res.ok) throw new Error(`HTTP error! status: ${res.status}`);
            const data = await res.json();
            setProducts(data || []);

            // compute min/max from product-level lowest price
            const allLowest = data.map(p => getLowestPrice(p)).filter(v => Number.isFinite(v));
            const minPrice = allLowest.length ? Math.min(...allLowest) : 0;
            const maxPrice = allLowest.length ? Math.max(...allLowest) : 100;
            const finalMaxPrice = maxPrice === 0 ? 100 : maxPrice;
            setMinMaxPrice([minPrice, finalMaxPrice]);
            setPriceRange([minPrice, finalMaxPrice]);
        } catch (e) {
            console.error('Error fetching products:', e);
            setErr(e.message || 'Błąd pobierania produktów');
        } finally {
            setIsLoading(false);
        }
    };

    useEffect(() => {
        loadProducts();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    // --- Modal Body Scroll Lock ---
    useEffect(() => {
        if (modalProduct) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'auto';
        }
        return () => document.body.style.overflow = 'auto';
    }, [modalProduct]);

    // --- Handlers ---
    const handleFilterChange = (e) => {
        const { name, value, type, checked } = e.target;
        setFilters(prev => ({ ...prev, [name]: type === 'checkbox' ? checked : value }));
    };

    const handlePriceInputChange = (index, value) => {
        const newPriceRange = [...priceRange];
        newPriceRange[index] = value;
        setPriceRange(newPriceRange);
    };

    const handlePriceInputBlur = () => {
        let [min, max] = priceRange.map(v => v === '' ? -1 : parseFloat(v));
        if (min === -1) min = minMaxPrice[0];
        if (max === -1) max = minMaxPrice[1];
        setPriceRange(min > max ? [max, min] : [min, max]);
    };

    const clearFilters = () => {
        setSearchTerm('');
        setFilters({ category: 'all', color: 'all', promotion: false, isNew: false });
        setSortOrder('default');
        setPriceRange(minMaxPrice);
    };

    // --- Memoized Filtering and Sorting ---
    const filteredAndSortedProducts = useMemo(() => {
        if (isLoading) return [];
        const [min, max] = priceRange.map(parseFloat);
        return products
            .filter(product => {
                const matchesCategory = filters.category === 'all' || product.category === filters.category;
                const matchesColor = filters.color === 'all' || (product.colorOptions || []).includes(filters.color);
                const matchesSearch = searchTerm === '' || (product.name || '').toLowerCase().includes(searchTerm.toLowerCase());
                const productPrice = getLowestPrice(product);
                const matchesPrice = productPrice >= min && productPrice <= max;
                const matchesPromotion = !filters.promotion || (filters.promotion && hasPromotion(product));
                const matchesNew = !filters.isNew || (filters.isNew && product.isNew);
                return matchesCategory && matchesColor && matchesSearch && matchesPrice && matchesPromotion && matchesNew;
            })
            .sort((a, b) => {
                const priceA = getLowestPrice(a);
                const priceB = getLowestPrice(b);
                switch (sortOrder) {
                    case 'price-asc': return priceA - priceB;
                    case 'price-desc': return priceB - priceA;
                    case 'name-asc': return (a.name || '').localeCompare(b.name || '');
                    case 'name-desc': return (b.name || '').localeCompare(a.name || '');
                    default: return 0;
                }
            });
    }, [searchTerm, filters, sortOrder, priceRange, products, isLoading]);

    const uniqueCategories = useMemo(() => getUniqueValues(products, 'category'), [products]);
    const uniqueColors = useMemo(() => getUniqueValues(products, 'colorOptions'), [products]);

    const FiltersPanel = () => (
        <div className="d-flex flex-column h-100">
            <div className="flex-grow-1">
                <div className="mb-3">
                    <label htmlFor="search" className="form-label fw-bold">Szukaj</label>
                    <input type="text" id="search" className="form-control" placeholder="Nazwa produktu..." value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)} />
                </div>
                <div className="mb-3">
                    <label htmlFor="sortOrder" className="form-label fw-bold">Sortuj</label>
                    <select id="sortOrder" className="form-select" value={sortOrder} onChange={(e) => setSortOrder(e.target.value)}>
                        <option value="default">Domyślnie</option>
                        <option value="price-asc">Cena: Rosnąco</option>
                        <option value="price-desc">Cena: Malejąco</option>
                        <option value="name-asc">Nazwa (A-Z)</option>
                        <option value="name-desc">Nazwa (Z-A)</option>
                    </select>
                </div>
                <div className="mb-3">
                    <label htmlFor="category" className="form-label fw-bold">Kategoria</label>
                    <select id="category" name="category" className="form-select" value={filters.category} onChange={handleFilterChange}>
                        {uniqueCategories.map(cat => <option key={cat} value={cat}>{cat === 'all' ? 'Wszystkie' : cat}</option>)}
                    </select>
                </div>
                <div className="mb-3">
                    <label htmlFor="color" className="form-label fw-bold">Kolor</label>
                    <select id="color" name="color" className="form-select" value={filters.color} onChange={handleFilterChange}>
                        {uniqueColors.map(col => <option key={col} value={col}>{col === 'all' ? 'Wszystkie' : col}</option>)}
                    </select>
                </div>
                <div className="mb-3">
                    <label className="form-label fw-bold">Cena</label>
                    <div className="d-flex align-items-center">
                        <input type="number" className="form-control" value={priceRange[0]} onChange={(e) => handlePriceInputChange(0, e.target.value)} onBlur={handlePriceInputBlur} placeholder="Min" />
                        <span className="mx-2">-</span>
                        <input type="number" className="form-control" value={priceRange[1]} onChange={(e) => handlePriceInputChange(1, e.target.value)} onBlur={handlePriceInputBlur} placeholder="Max" />
                    </div>
                </div>
                <div className="mb-3">
                    <div className="form-check">
                        <input className="form-check-input" type="checkbox" name="promotion" id="filterPromotion" checked={filters.promotion} onChange={handleFilterChange} />
                        <label className="form-check-label" htmlFor="filterPromotion">Pokaż promocje</label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="checkbox" name="isNew" id="filterNew" checked={filters.isNew} onChange={handleFilterChange} />
                        <label className="form-check-label" htmlFor="filterNew">Pokaż nowości</label>
                    </div>
                </div>
            </div>
            <div className="mt-auto">
                <button className="btn btn-primary w-100 mb-2" data-bs-dismiss="offcanvas">Pokaż produkty ({filteredAndSortedProducts.length})</button>
                <button className="btn btn-outline-secondary w-100" onClick={clearFilters}>Wyczyść wszystkie filtry</button>
            </div>
        </div>
    );

    const renderSkeletons = () => (
        <div className="row">
            {Array.from({ length: 6 }).map((_, index) => (
                <SkeletonCard key={index} />
            ))}
        </div>
    );

    return (
        <>
            <div className="offcanvas offcanvas-start" tabIndex="-1" id="filtersOffcanvas" aria-labelledby="filtersOffcanvasLabel">
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title" id="filtersOffcanvasLabel">Filtruj i Sortuj</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                </div>
                <div className="offcanvas-body">
                    <FiltersPanel />
                </div>
            </div>

            <div className="container mt-5 pt-5">
                <div className="text-center mb-4">
                    <h1 className="display-5">Nasze Produkty</h1>
                    <p className="lead text-muted">Przeglądaj naszą kolekcję unikalnych wydruków 3D.</p>
                </div>

                <div className="d-flex justify-content-between align-items-center mb-4">
                    <button className="btn btn-primary" type="button" data-bs-toggle="offcanvas" data-bs-target="#filtersOffcanvas" aria-controls="filtersOffcanvas">
                        Filtry
                    </button>
                    {!isLoading && <span className="text-muted">{filteredAndSortedProducts.length} z {products.length} produktów</span>}
                </div>

                {err && (
                    <div className="alert alert-danger">
                        Błąd ładowania produktów: {err}
                        <button className="btn btn-sm btn-outline-light ms-3" onClick={loadProducts}>Spróbuj ponownie</button>
                    </div>
                )}

                {isLoading ? renderSkeletons() : (
                    <div className="row">
                        {filteredAndSortedProducts.map(product => {
                            const cheapestOption = getCheapestOption(product);
                            const isPromo = hasPromotion(product);
                            return (
                                <div key={product.id} className="col-md-4 mb-4">
                                    <div className="card h-100 product-card">
                                        <div className="position-relative">
                                            <img src={resolveAsset(product.mainImage)} className="card-img-top product-card-img" alt={product.name} />
                                            <div className="position-absolute top-0 start-0 p-2">
                                                {product.isNew && <span className="badge bg-success me-1">NOWOŚĆ</span>}
                                                {isPromo && <span className="badge bg-danger me-1">PROMOCJA</span>}
                                                {product.isBestseller && <span className="badge bg-warning text-dark">BESTSELLER</span>}
                                            </div>
                                        </div>
                                        <div className="card-body">
                                            <h5 className="card-title">{product.name}</h5>
                                            {cheapestOption?.promoPrice ? (
                                                <p className="card-text">
                                                    od <s className="text-muted me-1">{cheapestOption.price}</s>
                                                    <span className="text-danger fw-bold ms-1">{cheapestOption.promoPrice}</span>
                                                </p>
                                            ) : (
                                                <p className="card-text text-muted">od {cheapestOption?.price}</p>
                                            )}
                                        </div>
                                        <div className="card-footer bg-transparent border-top-0 pb-3">
                                            <button onClick={() => setModalProduct(product)} className="btn btn-sm btn-outline-secondary me-2">Szybki podgląd</button>
                                            <Link to={`/products/${product.id}`} className="btn btn-sm btn-primary">Zobacz szczegóły</Link>
                                        </div>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                )}

                {modalProduct && (
                    <>
                        <div className="modal fade show" style={{ display: 'block' }} tabIndex="-1">
                            <div className="modal-dialog modal-lg modal-dialog-centered">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title">{modalProduct.name}</h5>
                                        <button type="button" className="btn-close" onClick={() => setModalProduct(null)}></button>
                                    </div>
                                    <div className="modal-body">
                                        <div className="row">
                                            <div className="col-md-6">
                                                <div className="position-relative">
                                                    <img src={resolveAsset(modalProduct.mainImage)} className="img-fluid rounded" alt={modalProduct.name} />
                                                    <div className="position-absolute top-0 start-0 p-2">
                                                        {modalProduct.isNew && <span className="badge bg-success me-1">NOWOŚĆ</span>}
                                                        {hasPromotion(modalProduct) && <span className="badge bg-danger me-1">PROMOCJA</span>}
                                                        {modalProduct.isBestseller && <span className="badge bg-warning text-dark">BESTSELLER</span>}
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <p>{modalProduct.description}</p>
                                                <h6>Opcje:</h6>
                                                <ul>{(modalProduct.heightOptions || []).map(opt => <li key={opt.label}>{opt.label} - <strong>{opt.promoPrice ? <><s className="text-muted me-2">{opt.price}</s><span className="text-danger">{opt.promoPrice}</span></> : opt.price}</strong></li>)}</ul>
                                                <h6>Dostępne kolory:</h6>
                                                <div className="d-flex flex-wrap mb-4">
                                                    {(modalProduct.colorOptions || []).map(color => (
                                                        <span
                                                            key={color}
                                                            className="color-swatch-detail me-2 mb-2"
                                                            style={{ backgroundColor: colorMap[color] || '#ccc' }}
                                                            title={color}
                                                        ></span>
                                                    ))}
                                                </div>
                                                <Link to={`/products/${modalProduct.id}`} className="btn btn-primary mt-3">Idź do strony produktu</Link>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="modal-backdrop fade show" onClick={() => setModalProduct(null)}></div>
                    </>
                )}
            </div>
        </>
    );
}

export default ProductsPage;
